import React from 'react';
import ErrorLog from './ErrorLog';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';

function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
}

export default class ErrorLogsDashboard extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      numberOfInnerEngulfings: '1',
      value: 0
    };
  }

  handleChangeNumberOfInnerEngulfings = (event) => {
    this.setState({
      numberOfInnerEngulfings: event.target.value,
    });
  };
  state = {
    value: 0,
  };

  handleChange = (event, value) => {
    this.setState({ value });
  };
  render() {
    const { value } = this.state;
    return (
      <div>
        <Tabs value={value} onChange={this.handleChange} scrollable scrollButtons="off">
          <Tab label="Trader API Errors" />
          <Tab label="Patterns API Errors" />
          <Tab label="MetatraderTerminal API Errors" />
          <Tab label="Assistance API Errors" />
          <Tab label="Trades API Errors" />
        </Tabs>
        {value === 0 && <TabContainer> <ErrorLog apiName="Trader" /></TabContainer>}
        {value === 1 && <TabContainer> <ErrorLog apiName="Patterns" /></TabContainer>}
        {value === 2 && <TabContainer> <ErrorLog apiName="MetatraderTerminal" /></TabContainer>}
        {value === 3 && <TabContainer> <ErrorLog apiName="Assistance" /></TabContainer>}
        {value === 4 && <TabContainer> <ErrorLog apiName="Trades" /></TabContainer>}
      </div>
    );
  }
}