import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import _ from 'lodash';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';
import TextField from '@material-ui/core/TextField';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelActions from '@material-ui/core/ExpansionPanelActions';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import ReactQuill from 'react-quill';
import 'react-quill/dist/quill.snow.css'; // ES6
import '../css/positions.css'
import BlockUi from 'react-block-ui';
import 'react-block-ui/style.css';


const styles = theme => ({
  root: {
    width: '100%',
    overflow: 'none',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
  icon: {
    verticalAlign: 'bottom',
    height: 10,
    width: 10,
  },
  details: {
    alignItems: 'center',
  },
  column: {
    flexBasis: '33.33%',
  },
  helper: {
    borderLeft: `2px solid ${theme.palette.divider}`,
    padding: `${theme.spacing.unit}px ${theme.spacing.unit * 2}px`,
  },
  link: {
    color: theme.palette.primary.main,
    textDecoration: 'none',
    '&:hover': {
      textDecoration: 'underline',
    },
  },
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
  },
});



class Positions extends React.Component {

  constructor(props) {
    super(props);
    this.getAllResultValues = this.getAllResultValues.bind(this);
    this.handleEditorChange = this.handleEditorChange.bind(this)
    this.toggleBlocking = this.toggleBlocking.bind(this);

    this.state = {
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      hasPendingHttp: false,
      blocking: false,
    };
  }
  modules = {
    toolbar: [
      [{ 'header': [1, 2, false] }],
      ['bold', 'italic', 'underline', 'strike', 'blockquote'],
      [{ 'list': 'ordered' }, { 'list': 'bullet' }, { 'indent': '-1' }, { 'indent': '+1' }],
      ['link', 'image', 'video'],
      ['clean']
    ],
  };

  formats = [
    'header',
    'bold', 'italic', 'underline', 'strike', 'blockquote',
    'list', 'bullet', 'indent',
    'link', 'image'
  ];

  toggleBlocking() {
    this.setState({ blocking: !this.state.blocking });
  }

  handleEditorChange = id => event => {
    this.handleEditorChangeInArray(id, event);
  }

  handleEditorChangeInArray(id, notesUpdated) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        newArray[i].notes = notesUpdated;
        break; //Stop this loop, we found it!
      }
    }
    this.setState({ resultsList: newArray });
  }

  save(id) {
    for (var i in this.state.resultsList) {
      if (this.state.resultsList[i].id === id) {
        this.modifyTicketDetails(this.state.resultsList[i]);
        break; //Stop this loop, we found it!
      }
    }
  }

  handleChange = (name, id) => event => {
    if (name === 'flagged') {
      this.handleFlaggedChangeInArray(id, event.target.checked);
    } else if (name === 'pendingAction') {
      this.handlePendingActionChangeInArray(id, event.target.checked);
    }
  };

  handleFlaggedChangeInArray(id, valueUpdated) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        newArray[i].flagged = valueUpdated;
        break; //Stop this loop, we found it!
      }
    }
    this.setState({ resultsList: newArray });
  }

  handlePendingActionChangeInArray(id, valueUpdated) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        newArray[i].pendingAction = valueUpdated;
        break; //Stop this loop, we found it!
      }
    }
    this.setState({ resultsList: newArray });
  }

  handleTextFieldChange = (name, id) => event => {
    if (name === 'dynamicStoplossPrice') {
      this.handleDynamicStoplossPriceChangeInArray(id, event.target.value);
    } else if (name === 'comment') {
      this.handleCommentChangeInArray(id, event.target.value);
    } if (name === 'lockTimeframe') {
      this.handleLockTimeframeChangeInArray(id, event.target.value);
    }

  };

  handleLockTimeframeChangeInArray(id, valueUpdated) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        newArray[i].lockTimeframe = valueUpdated;
        break; //Stop this loop, we found it!
      }
    }
    this.setState({ resultsList: newArray });
  }

  handleCommentChangeInArray(id, valueUpdated) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        newArray[i].comment = valueUpdated;
        break; //Stop this loop, we found it!
      }
    }
    this.setState({ resultsList: newArray });
  }

  handleDynamicStoplossPriceChangeInArray(id, valueUpdated) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        newArray[i].dynamicStoplossPrice = valueUpdated;
        break; //Stop this loop, we found it!
      }
    }
    this.setState({ resultsList: newArray });
  }

  render() {

    if (this.state.isFetching) return <Loading />;
    if (this.state.hasHttpError)
      return <PaperSheet color="error" header={this.props.timeframe} message={this.state.errorMessage} />;

    var allResultValues = _.map(this.state.resultsList, (resultValue) => {
      const { classes } = this.props;
      return <ListItem key={resultValue.ticket}>

        <div className={classes.root}>
          <ExpansionPanel>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <div>
                <Typography className={classes.heading}>{resultValue.symbol}</Typography>
              </div>
              <div>
                <Typography className={classes.secondaryHeading}>{'____ IsBullish: ' + resultValue.isBullish + ';____IsMt5:' + resultValue.isMt5 + '____;Comment: ' + resultValue.comment}</Typography>
              </div>
            </ExpansionPanelSummary>
            <div>
              <ExpansionPanelDetails>
                <form>
                  <TextField
                    id={'symbol_' + resultValue.id}
                    label="Symbol"
                    className={classes.textField}
                    defaultValue={resultValue.symbol}
                    onChange={this.handleTextFieldChange('symbol', resultValue.id)}
                    margin="normal"
                    variant="outlined"
                  />
                  <TextField
                    id={'lockTimeframe_' + resultValue.id}
                    label="Lock Timeframe"
                    className={classes.textField}
                    defaultValue={resultValue.lockTimeframe}
                    onChange={this.handleTextFieldChange('lockTimeframe', resultValue.id)}
                    margin="normal"
                    variant="outlined"
                  />
                  <TextField
                    id={'dynamicStoplossPrice_' + resultValue.id}
                    label="Dynamic Stoploss Price"
                    className={classes.textField}
                    defaultValue={resultValue.dynamicStoplossPrice}
                    onChange={this.handleTextFieldChange('dynamicStoplossPrice', resultValue.id)}
                    margin="normal"
                    variant="outlined"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={resultValue.flagged}
                        onChange={this.handleChange('flagged', resultValue.id)}
                        defaultValue="flagged"
                        color="primary"
                      />
                    }
                    label="Flag"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={resultValue.pendingAction}
                        onChange={this.handleChange('pendingAction', resultValue.id)}
                        defaultValue="pendingAction"
                        color="primary"
                      />
                    }
                    label="Pending Action"
                  />
                  <TextField
                    id={'comment_' + resultValue.id}
                    label="Comment"
                    style={{ margin: 8 }}
                    placeholder="Placeholder"
                    onChange={this.handleTextFieldChange('comment', resultValue.id)}
                    defaultValue={resultValue.comment}
                    fullWidth
                    margin="normal"
                    variant="outlined"
                  />
                  <Button size="small" color="primary" onClick={() =>
                    this.postSuspendedTrade(resultValue.symbol, resultValue.openTimeframe)}>
                    Suspended Trades For ({resultValue.symbol + '  <==> ' + resultValue.openTimeframe})
                  </Button>
                </form>

              </ExpansionPanelDetails>
              <Divider />
              <ExpansionPanelDetails id="FXMuiExpansionPanelDetails" classes={{ root: 'FXMuiExpansionPanelDetails-root' }}>

                <ReactQuill id={'notes_' + resultValue.id} theme="snow" modules={this.modules}
                  formats={this.formats} defaultValue={resultValue.notes}
                  onChange={this.handleEditorChange(resultValue.id)} />

              </ExpansionPanelDetails>
            </div>

            <div>
              <Divider />
              <ExpansionPanelActions>
                <Button size="small">Cancel/Clear</Button>
                <Button size="small" color="primary" onClick={() => this.save(resultValue.id)}>
                  Save
          </Button>
              </ExpansionPanelActions>
            </div>
          </ExpansionPanel>
        </div>


      </ListItem>;
    });
    return (
      <div>
        <BlockUi tag="div" blocking={this.state.blocking}>
          <List subheader={<ListSubheader component="div">{'Positions (' + this.state.totalPairs + ')'}</ListSubheader>}>
            {allResultValues}
          </List>
        </BlockUi>
      </div>
    );
  }

  postSuspendedTrade(symbol, timeframe) {
    var url = BACKEND_BASE_URL + 'trader/SuspendedTrades';
    var body = {
      timeframe: timeframe,
      symbol: symbol
    };
    this.toggleBlocking();
    fetch(url, {
      method: 'POST',
      body: JSON.stringify(body),
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
      .then(response => response.json())
      .then(data => {
        this.toggleBlocking();
        this.forceUpdate();
      }).catch((error) => {
        this.setState({
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  modifyTicketDetails(ticketdetails) {
    var url = BACKEND_BASE_URL + this.props.apiName + '/ticketdetails';
    this.setState({
      hasPendingHttp: true
    });
    fetch(url, {
      method: 'PUT',
      body: JSON.stringify(ticketdetails),
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
      .then(response => response.json())
      .then(data => {
        this.setState({
          hasPendingHttp: false
        });
      }).catch((error) => {
        this.setState({
          hasPendingHttp: false,
          errorMessage: error.toString()
        });
      });
  }

  getAllResultValues() {
    var url = BACKEND_BASE_URL + this.props.apiName + '/ticketdetails/' + this.props.type;

    this.setState({
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      resultsList: []
    });
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.setState({
          resultsList: data,
          totalPairs: data.length,
          isFetching: false,
          hasHttpError: false
        });
      }).catch((error) => {
        this.setState({
          isFetching: false,
          hasHttpError: true,
          errorMessage: error.toString()
        });

      });
  }
  componentDidMount() {
    this.getAllResultValues();
  }
}

Positions.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Positions);