import React,  {Fragment} from 'react';
import Doji from './Doji';

const styles = {
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
  },
  gridList: {
    display: 'flex',
    flexWrap: 'nowrap',
    overflowX: 'auto',
  },
  titleStyle: {
    color: 'rgb(0, 188, 212)',
  },
};

export default class DojisPage extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        numberOfInnerEngulfings: '1',
      };
    }
    state = {
      selectedTab: 'a',
    }
  
    handleTabClick = (event, value) => {
      this.setState({selectedTab: value});
    }

  render() {
    return (
    <Fragment>
      <div style={styles.root} >
          <Doji timeframe="PERIOD_MN1"/> 
          <Doji timeframe="PERIOD_W1"/>
          <Doji timeframe="PERIOD_D1"/>  
          <Doji timeframe="PERIOD_H4"/>  
          <Doji timeframe="PERIOD_H1"/>  
    </div>
    </Fragment>
    );
  }
}