import React from 'react';
import EngulfingsNonFreshPage from './EngulfingsNonFreshPage';
import EngulfingsPage from './EngulfingsPage';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import NestedEngulfingsPage from './NestedEngulfingsPage';

function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
}

export default class EngulfingsDashboard extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      numberOfInnerEngulfings: '1',
      value: 0
    };
  }

  handleChangeNumberOfInnerEngulfings = (event) => {
    this.setState({
      numberOfInnerEngulfings: event.target.value,
    });
  };
  state = {
    value: 0,
  };

  handleChange = (event, value) => {
    this.setState({ value });
  };
  render() {
    const { value } = this.state;
    return (
      <div>
        <Tabs value={value} onChange={this.handleChange} scrollable scrollButtons="off">
          <Tab label="Fresh Engulfings" />
          <Tab label="Non-Fresh Engulfings" />
          <Tab label="Fresh Nested Engulfings" />
          <Tab label="Non-Fresh Nested Engulfings" />
        </Tabs>
        {value === 0 && <TabContainer> <EngulfingsPage /></TabContainer>}
        {value === 1 && <TabContainer> <EngulfingsNonFreshPage /></TabContainer>}
        {value === 2 && <TabContainer> <NestedEngulfingsPage isFreshTrades="true" /></TabContainer>}
        {value === 3 && <TabContainer> <NestedEngulfingsPage isFreshTrades="false" /></TabContainer>}
      </div>
    );
  }
}