import React  from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItemText from '@material-ui/core/ListItemText';
import _ from 'lodash';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

const styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 200,
  },
});

export default class Doji extends React.Component {

  constructor(props) {
    super(props);
    this.getAllResultValues = this.getAllResultValues.bind(this);
    this.state = {
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      timeseriesIndex: 0
    };
  }

  handleChangeIndex = (event) => {
    this.setState({
      timeseriesIndex: event.target.value,
    });
  };

  render() {
    if (this.state.isFetching) return <Loading />;
    if (this.state.hasHttpError)
      return <PaperSheet color="error" header={this.props.timeframe} message={this.state.errorMessage} />;

    var allResultValues = _.map(this.state.resultsList, (resultValue) => {
      return <ListItem
        key={resultValue.id}
      >
        <ListItemText primary={resultValue.symbol} secondary={
          <React.Fragment>
          {'IsBullish: ' + resultValue.isBullish}
        </React.Fragment>
        } />
      </ListItem>;
    });
    if (this.props.nonfresh !== true && this.props.nonfresh !== 'true') {
      return (
        <List subheader={<ListSubheader component="div">{'' + this.props.timeframe + ' (' + this.state.totalPairs + ') : Index [' + this.state.timeseriesIndex + ']'}</ListSubheader>}>
          <div>
            <TextField
              label="Timeseries Index"
              id="handleChangeIndex-none"
              onChange={this.handleChangeIndex}
              value={this.state.timeseriesIndex}
              className={styles.textField}
            />

          </div>
          <Button size="small" className={styles.button} onClick={this.getAllResultValues}>Refresh</Button>

          {allResultValues}
        </List>
      );
    } else {
      return (
        <List subheader={<ListSubheader component="div">{'' + this.props.timeframe + ' (' + this.state.totalPairs + ')'}</ListSubheader>}>
          {allResultValues}
        </List>
      );
    }
  }

  getAllResultValues() {
    var url = BACKEND_BASE_URL + 'patterns/Dojis/' + this.props.timeframe + '/timeseries/' + this.state.timeseriesIndex;

    this.setState({
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      resultsList: []
    });
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.setState({
          resultsList: data,
          totalPairs: data.length,
          isFetching: false,
          hasHttpError: false
        });
      }).catch((error) => {
        this.setState({
          isFetching: false,
          hasHttpError: true,
          errorMessage: error.toString()
        });

      });
  }
  componentDidMount() {
    this.getAllResultValues();
  }
}