import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItemText from '@material-ui/core/ListItemText';
import _ from 'lodash';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';
import Typography from '@material-ui/core/Typography';

export default class NestedEngulfing extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      nestedengulfingsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: ''
    };
  }

  getOpposingTimeframes(opposingTimeframes, type) {
    var text = type + ' Opposing: ';
    var hasAtleastOne = false;
    for (var i in opposingTimeframes) {
      if(hasAtleastOne === true){
      text = text + ', ' +opposingTimeframes[i].timeframe.substring(7);
      }else{
        text = text + opposingTimeframes[i].timeframe.substring(7);
      }
      hasAtleastOne = true;
    }
    if(hasAtleastOne){
    return text;
    }else{
      return '';
    }
  }

  getAgreeingTimeframes(agreeingTimeframes, type) {
    var text = type + ' Agreeing: ';
    var hasAtleastOne = false;
    for (var i in agreeingTimeframes) {
      if(hasAtleastOne === true){
      text = text + ', ' +agreeingTimeframes[i].timeframe.substring(7);
      }else{
        text = text + agreeingTimeframes[i].timeframe.substring(7);
      }
      hasAtleastOne = true;
    }
    if(hasAtleastOne){
    return text;
    }else{
      return '';
    }
  }

  render() {
    if (this.state.isFetching) return <Loading />;
    if (this.state.hasHttpError)
      return <PaperSheet color="error" header={this.props.timeframe} message={this.state.errorMessage} />;

    var allNestedengulfings = _.map(this.state.nestedengulfingsList, (nestedengulfing) => {
      return <ListItem
        key={nestedengulfing.id}
      >
        <ListItemText primary={nestedengulfing.symbol} secondary={
          <React.Fragment>
          {'IsBullish: ' + nestedengulfing.isBullish}
          <Typography
            component="span"
            variant="body2"
            color="error"
          >
            {this.getOpposingTimeframes(nestedengulfing.outerEngulfing.opposingTimeframes, 'Outer')}
          </Typography>
          <Typography
            component="span"
            variant="body2"
            color="primary"
          >
            {this.getAgreeingTimeframes(nestedengulfing.outerEngulfing.agreeingTimeframes, 'Outer')}
          </Typography>

          <Typography
            component="span"
            variant="body2"
          >
            -------------------
          </Typography>

          <Typography
            component="span"
            variant="body2"
            color="error"
          >
            {this.getOpposingTimeframes(nestedengulfing.innerEngulfing.opposingTimeframes, 'Inner')}
          </Typography>
          <Typography
            component="span"
            variant="body2"
            color="primary"
          >
            {this.getAgreeingTimeframes(nestedengulfing.innerEngulfing.agreeingTimeframes, 'Inner')}
          </Typography>
        </React.Fragment>
          } />
      </ListItem>;
    });
    return (
      <List subheader={<ListSubheader component="div">{'' + this.props.timeframe + ' (' + this.state.totalPairs + ')'}</ListSubheader>}>
        {allNestedengulfings}
      </List>
    );
  }

  getAllNestedengulfings() {
    var url = BACKEND_BASE_URL + 'patterns/nestedengulfings/' + this.props.timeframe + '/type/' + this.props.isFreshTrades;
    this.setState({
      isFetching: true,
      hasHttpError: false,
      errorMessage: ''
    });
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.setState({
          nestedengulfingsList: data,
          totalPairs: data.length,
          isFetching: false,
          hasHttpError: false
        });
      }).catch((error) => {
        this.setState({
          isFetching: false,
          hasHttpError: true,
          errorMessage: error.toString()
        });

      });
  }
  componentDidMount() {
    this.getAllNestedengulfings();
  }
}