import React, { Fragment } from 'react';
import NestedEngulfing from './NestedEngulfing';
import PaperSheet from './PaperSheet';
import Divider from '@material-ui/core/Divider';
const styles = {
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
  },
  gridList: {
    display: 'flex',
    flexWrap: 'nowrap',
    overflowX: 'auto',
  },
  titleStyle: {
    color: 'rgb(0, 188, 212)',
  },
};

export default class NestedEngulfingsPage extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      numberOfInnerEngulfings: '1',
    };
  }
  state = {
    selectedTab: 'a',
  }

  handleTabClick = (event, value) => {
    this.setState({ selectedTab: value });
  }

  handleChangeNumberOfInnerEngulfings = (event) => {
    this.setState({
      numberOfInnerEngulfings: event.target.value,
    });
  };

  render() {
    //var messageHeader = "NESTED ENGULFINGS - IsFreshTrades: " + this.props.isFreshTrades + "; NumberOfInnerEngulfings: MN1(1) H4(1), D1(1), W1(1)";
    return (
      <Fragment>
        <PaperSheet color="primary" header="" message="" />
        <Divider />
        <div style={styles.root} >
          <NestedEngulfing isFreshTrades={this.props.isFreshTrades} timeframe="PERIOD_MN1" numberOfInnerEngulfings={1} />
          <NestedEngulfing isFreshTrades={this.props.isFreshTrades} timeframe="PERIOD_W1" numberOfInnerEngulfings={1} />
          <NestedEngulfing isFreshTrades={this.props.isFreshTrades} timeframe="PERIOD_D1" numberOfInnerEngulfings={1} />
          <NestedEngulfing isFreshTrades={this.props.isFreshTrades} timeframe="PERIOD_H4" numberOfInnerEngulfings={1} />

        </div>
      </Fragment>
    );
  }
}