import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import Button from '@material-ui/core/Button';
import ListItemText from '@material-ui/core/ListItemText';

import Quote from './components/Quote'
import Schedules from './components/Schedules'
import Quotes from './components/Quotes'
import AuditsDashboard from './components/AuditsDashboard'
import ErrorLogsDashboard from './components/ErrorLogsDashboard'
import AssistancePositions from './components/AssistancePositions';
import EngulfingsDashboard from './components/EngulfingsDashboard';
import About from './components/About'
import RobotPositions from './components/RobotPositions';
import SuspendedTrades from './components/SuspendedTrades';
import Symbols from './components/Symbols';
import DojisPage from './components/DojisPage';

const drawerWidth = 240;

const styles = theme => ({
  root: {
    display: 'flex',
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginLeft: 12,
    marginRight: 20,
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: '0 8px',
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing.unit * 0.1,
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
});

class Layout extends React.Component {
  state = {
    open: false,
    page: 'RobotPositions'
  };

  handleDrawerOpen = () => {
    this.setState({ open: true });
  };

  handleDrawerClose = () => {
    this.setState({ open: false });
  };

  changePage = (name) => () => {
    this.setState({
      page: name,
    });
  };

  getYear() {
    return new Date().getFullYear();
}

  render() {
    const { classes, theme } = this.props;
    const { open } = this.state;

    return (
      <div className={classes.root}>
        <CssBaseline />
        <AppBar
          position="fixed"
          className={classNames(classes.appBar, {
            [classes.appBarShift]: open,
          })}
          style={{ whiteSpace: 'nowrap' }}
        >
          <Toolbar disableGutters={!open}>
            <IconButton
              color="inherit"
              aria-label="Open drawer"
              onClick={this.handleDrawerOpen}
              className={classNames(classes.menuButton, open && classes.hide)}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" color="inherit" noWrap>
              I Am A Bot Made To Assist On FX!
            </Typography>
          </Toolbar>
        </AppBar>

        <Drawer
          className={classes.drawer}
          variant="persistent"
          anchor="left"
          open={open}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
          <div className={classes.drawerHeader}>
            <IconButton onClick={this.handleDrawerClose}>
              {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
            </IconButton>
          </div>
          <Divider />
          <List>
            <div style={{ backgroundColor: this.state.page === 'RobotPositions' ? 'grey' : 'white' }}>
              <ListItem id="RobotPositions" onClick={this.changePage('RobotPositions')} button key="RobotPositions"  >
                <ListItemText primary="Robot Positions" />
              </ListItem>
            </div>

            <div style={{ backgroundColor: this.state.page === 'AssistancePositions' ? 'grey' : 'white' }}>
              <ListItem id="AssistancePositions" onClick={this.changePage('AssistancePositions')} button key="AssistancePositions"  >
                <ListItemText primary="Assistance Positions" />
              </ListItem>
            </div>

            <div style={{ backgroundColor: this.state.page === 'Engulfing' ? 'grey' : 'white' }}>
              <ListItem id="Engulfing" onClick={this.changePage('Engulfing')} button key="Engulfing"  >
                <ListItemText primary="Engulfings" />
              </ListItem>
            </div>

            <div style={{ backgroundColor: this.state.page === 'Doji' ? 'grey' : 'white' }}>
              <ListItem id="Doji" onClick={this.changePage('Doji')} button key="Doji"  >
                <ListItemText primary="Dojis" />
              </ListItem>
            </div>

            <div style={{ backgroundColor: this.state.page === 'AuditsDashboard' ? 'grey' : 'white' }}>
              <ListItem id="AuditsDashboard" onClick={this.changePage('AuditsDashboard')} button key="AuditsDashboard"  >
                <ListItemText primary="Audits" />
              </ListItem>
            </div>

            <div style={{ backgroundColor: this.state.page === 'ErrorLogsDashboard' ? 'grey' : 'white' }}>
              <ListItem id="ErrorLogsDashboard" onClick={this.changePage('ErrorLogsDashboard')} button key="ErrorLogsDashboard"  >
                <ListItemText primary="APIs Errors" />
              </ListItem>
            </div>

            <div style={{ backgroundColor: this.state.page === 'SuspendedTrades' ? 'grey' : 'white' }}>
              <ListItem id="SuspendedTrades" onClick={this.changePage('SuspendedTrades')} button key="SuspendedTrades"  >
                <ListItemText primary="Suspended Trades" />
              </ListItem>
            </div>

            <div style={{ backgroundColor: this.state.page === 'Schedules' ? 'grey' : 'white' }}>
              <ListItem id="Schedules" onClick={this.changePage('Schedules')} button key="Schedules"  >
                <ListItemText primary="Schedules" />
              </ListItem>
            </div>

            <div style={{ backgroundColor: this.state.page === 'Symbols' ? 'grey' : 'white' }}>
              <ListItem id="Symbols" onClick={this.changePage('Symbols')} button key="Symbols"  >
                <ListItemText primary="Symbols" />
              </ListItem>
            </div>

            <div style={{ backgroundColor: this.state.page === 'Quotes' ? 'grey' : 'white' }}>
              <ListItem id="Quotes" onClick={this.changePage('Quotes')} button key="Quotes"  >
                <ListItemText primary="Quotes" />
              </ListItem>
            </div>

            <Divider />

            <div style={{ backgroundColor: this.state.page === 'About' ? 'grey' : 'white' }}>
              <ListItem id="About" onClick={this.changePage('About')} button key="About"  >
                <ListItemText primary="About Me" />
              </ListItem>
            </div>

          </List>

        </Drawer>
        <main
          className={classNames(classes.content, {
            [classes.contentShift]: open,
          })}
        >
          <div className={classes.drawerHeader} />
          <Quote />
          {this.state.page === 'Quotes' && <Quotes />}
          {this.state.page === 'Schedules' && <Schedules />}
          {this.state.page === 'ErrorLogsDashboard' && <ErrorLogsDashboard />}
          {this.state.page === 'Engulfing' && <EngulfingsDashboard />}
          {this.state.page === 'Doji' && <DojisPage />}
          {this.state.page === 'AuditsDashboard' && <AuditsDashboard />}
          {this.state.page === 'AssistancePositions' && <AssistancePositions />}
          {this.state.page === 'RobotPositions' && <RobotPositions />}
          {this.state.page === 'SuspendedTrades' && <SuspendedTrades />}
          {this.state.page === 'Symbols' && <Symbols />}
          {this.state.page === 'About' && <About />}
          < Divider />
          <Typography>This application was created by <a href="https://sitholewb.github.io/#/"> Welcome Bonginhlahla Sithole </a>
          <span>
              {this.getYear()}
          </span>
           </Typography>
          < Divider />
        </main>

      </div>

    );
  }
}

Layout.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(Layout);
