import React from 'react';
import PropTypes from 'prop-types';
import TextField from '@material-ui/core/TextField';
import { withStyles } from '@material-ui/core/styles';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';
import ExpansionPanelActions from '@material-ui/core/ExpansionPanelActions';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import ReactQuill from 'react-quill';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import _ from 'lodash';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';
import 'react-quill/dist/quill.snow.css'; // ES6
import '../css/positions.css'
import BlockUi from 'react-block-ui';
import 'react-block-ui/style.css';

const styles = theme => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
  },
  dense: {
    marginTop: 16,
  },
  menu: {
    width: 200,
  },
  root: {
    width: '100%',
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '33.33%',
    flexShrink: 0,
  },
  secondaryHeading: {
    fontSize: theme.typography.pxToRem(15),
    color: theme.palette.text.secondary,
  },
});


class TradeDetailsOpen extends React.Component {

  constructor(props) {
    super(props);
    this.getAllResultValues = this.getAllResultValues.bind(this);
    this.handleEditorChange = this.handleEditorChange.bind(this);
    this.handleSymbolChange = this.handleSymbolChange.bind(this);
    this.saveNewSymbol = this.saveNewSymbol.bind(this);
    this.toggleBlocking = this.toggleBlocking.bind(this);

    this.state = {
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      hasPendingHttp: false,
      expanded: null,
      symbol: '',
      blocking: false,
    };
  }
  modules = {
    toolbar: [
      [{ 'header': [1, 2, false] }],
      ['bold', 'italic', 'underline', 'strike', 'blockquote'],
      [{ 'list': 'ordered' }, { 'list': 'bullet' }, { 'indent': '-1' }, { 'indent': '+1' }],
      ['link', 'image', 'video'],
      ['clean']
    ],
  };

  formats = [
    'header',
    'bold', 'italic', 'underline', 'strike', 'blockquote',
    'list', 'bullet', 'indent',
    'link', 'image'
  ];

  toggleBlocking() {
    this.setState({ blocking: !this.state.blocking });
  }

  handleEditorChange = id => event => {
    this.handleEditorChangeInArray(id, event);
  }

  handleEditorChangeInArray(id, notesUpdated) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        newArray[i].notes = notesUpdated;
        break; //Stop this loop, we found it!
      }
    }
    this.setState({ resultsList: newArray });
  }

  save(id) {
    for (var i in this.state.resultsList) {
      if (this.state.resultsList[i].id === id) {
        this.modifyWatchSymbols(this.state.resultsList[i]);
        break; //Stop this loop, we found it!
      }
    }
  }

  handleDeletedChange = id => event => {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        newArray[i].isDeleted = event.target.checked;
        break; //Stop this loop, we found it!
      }
    }
    this.setState({ resultsList: newArray });
  }


  handleChange = panel => (event, expanded) => {
    this.setState({
      expanded: expanded ? panel : false,
    });
  }

  handleTextFieldChangeInArray = (name, id) => event => {
    if (name === 'alertOnTouch') {
      this.handleAlertOnTouchChangeInArray(id, event.target.value);
    } else if (name === 'comment') {
      this.handleCommentChangeInArray(id, event.target.value);
    }

  };

  handleAlertOnTouchChangeInArray(id, valueUpdated) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        newArray[i].alertOnTouch = valueUpdated;
        break; //Stop this loop, we found it!
      }
    }
    this.setState({ resultsList: newArray });
  }

  handleCommentChangeInArray(id, valueUpdated) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        newArray[i].comment = valueUpdated;
        break; //Stop this loop, we found it!
      }
    }
    this.setState({ resultsList: newArray });
  }

  handleTextFieldChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  handleSymbolChange = event => {
    this.setState({
      symbol: event.target.value,
    });
  };

  render() {
    if (this.state.isFetching) return <Loading />;
    if (this.state.hasHttpError)
      return <PaperSheet color="error" header={this.props.timeframe} message={this.state.errorMessage} />;

    var allResultValues = _.map(this.state.resultsList, (resultValue) => {
      const { classes } = this.props;
      return <ListItem key={resultValue.id}>
        <div className={classes.root}>
          <ExpansionPanel>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <div className={classes.column}>
                <Typography className={classes.heading}>{resultValue.symbol}</Typography>
              </div>
              <div className={classes.column}>
                <Typography className={classes.secondaryHeading}>{' :    Comment: ' + resultValue.comment}</Typography>
              </div>
            </ExpansionPanelSummary>
            <div>
              <ExpansionPanelDetails>
                <form>
                  <TextField
                    id={'symbol_' + resultValue.id}
                    label="Symbol"
                    className={classes.textField}
                    defaultValue={resultValue.symbol}
                    onChange={this.handleTextFieldChangeInArray('symbol', resultValue.id)}
                    margin="normal"
                    variant="outlined"
                  />
                  <TextField
                    id={'alertOnTouch_' + resultValue.id}
                    label="Alert Price On Touch"
                    className={classes.textField}
                    defaultValue={resultValue.alertOnTouch}
                    onChange={this.handleTextFieldChangeInArray('alertOnTouch', resultValue.id)}
                    margin="normal"
                    variant="outlined"
                  />
                  <FormControlLabel
                    control={
                      <Checkbox
                        checked={resultValue.deleted}
                        onChange={this.handleDeletedChange(resultValue.id)}
                        value="deleted"
                        color="primary"
                      />
                    }
                    label="Delete"
                  />

                  <TextField
                    id={'comment_' + resultValue.id}
                    label="Comment"
                    style={{ margin: 8 }}
                    placeholder="Placeholder"
                    onChange={this.handleTextFieldChangeInArray('comment', resultValue.id)}
                    defaultValue={resultValue.comment}
                    fullWidth
                    margin="normal"
                    variant="outlined"
                  />
                </form>

              </ExpansionPanelDetails>
              <Divider />
              <ExpansionPanelDetails id="FXMuiExpansionPanelDetails" classes={{ root: 'FXMuiExpansionPanelDetails-root' }}>

                <ReactQuill id={'notes_' + resultValue.id} theme="snow" modules={this.modules}
                  formats={this.formats} defaultValue={resultValue.notes}
                  onChange={this.handleEditorChange(resultValue.id)} />

              </ExpansionPanelDetails>
            </div>

            <div>
              <Divider />
              <ExpansionPanelActions>
                <Button size="small" color="primary" onClick={() => this.save(resultValue.id)}>
                  Save
          </Button>
              </ExpansionPanelActions>
            </div>
          </ExpansionPanel>
        </div>
      </ListItem>;
    });
    return (

      <div>
        <BlockUi tag="div" blocking={this.state.blocking}>

          <ExpansionPanel>
            <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
              <div >
                <Typography className={styles.heading}>{''}</Typography>
              </div>
              <div className={styles.column}>
                <Typography className={styles.secondaryHeading}>{'Expand to add new notes.'}</Typography>
              </div>
            </ExpansionPanelSummary>
            <div>
              <ExpansionPanelDetails>

                <form>
                  <TextField
                    id={'symbol_'}
                    label="Symbol"
                    defaultValue={this.state.symbol}
                    onChange={this.handleSymbolChange}
                    margin="normal"
                    variant="outlined"
                  />
                </form>


              </ExpansionPanelDetails>
            </div>

            <div>
              <Divider />
              <ExpansionPanelActions>
                <Button size="small" color="primary" onClick={() => this.saveNewSymbol()}>
                  Save To Watch List
          </Button>
              </ExpansionPanelActions>
            </div>
          </ExpansionPanel>

          <List subheader={<ListSubheader component="div">{'Watch List (' + this.state.totalPairs + ')'}</ListSubheader>}>
            {allResultValues}
          </List>
        </BlockUi>
      </div>
    );
  }

  saveNewSymbol() {
    var url = BACKEND_BASE_URL + 'assistance/WatchSymbols';
    var body = {
      symbol: this.state.symbol,
    };
    this.toggleBlocking();
    fetch(url, {
      method: 'POST',
      body: JSON.stringify(body),
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
      .then(response => response.json())
      .then(data => {
        this.setState({ symbol: '' });
        this.getAllResultValues();
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }
  modifyWatchSymbols(watchSymbol) {
    var url = BACKEND_BASE_URL + 'assistance/WatchSymbols';
    this.setState({
      hasPendingHttp: true
    });
    this.toggleBlocking();
    fetch(url, {
      method: 'PUT',
      body: JSON.stringify(watchSymbol),
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
      .then(response => response.json())
      .then(data => {
        this.setState({
          hasPendingHttp: false
        });
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          hasPendingHttp: false,
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  getAllResultValues() {
    var url = BACKEND_BASE_URL + 'assistance/WatchSymbols';

    this.setState({
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      resultsList: []
    });
    this.toggleBlocking();
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.setState({
          resultsList: data,
          totalPairs: data.length,
          isFetching: false,
          hasHttpError: false
        });
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          isFetching: false,
          hasHttpError: true,
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }


  componentDidMount() {
    this.getAllResultValues();
  }
}
TradeDetailsOpen.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(TradeDetailsOpen);