import React from 'react';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';
import _ from 'lodash';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ListItemText from '@material-ui/core/ListItemText';
import BlockUi from 'react-block-ui';
import 'react-block-ui/style.css';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import ExpansionPanelActions from '@material-ui/core/ExpansionPanelActions';
import TextField from '@material-ui/core/TextField';
import { isNullOrUndefined } from 'util';
import Delete from '@material-ui/icons/Delete';
import Save from '@material-ui/icons/Save';
import classNames from 'classnames';

export default class Symbols extends React.Component {

  constructor(props) {
    super(props);
    this.getSymbols = this.getSymbols.bind(this);
    this.postSymbol = this.postSymbol.bind(this);
    this.removeSymbol = this.removeSymbol.bind(this);
    this.toggleBlocking = this.toggleBlocking.bind(this);
    this.state = {
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      hasPendingHttp: false,
      name: '',
      description: '',
      blocking: false,
      editId: '',
      editMode: false,
      PERIOD_M15: '',
      PERIOD_M30: '',
      PERIOD_H1: '',
      PERIOD_H4: '',
      PERIOD_D1: '',
      PERIOD_W1: ''
    };
  }

  toggleBlocking() {
    this.setState({ blocking: !this.state.blocking });
  }

  handleTextFieldChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  render() {
    if (this.state.isFetching) return <Loading />;
    if (this.state.hasHttpError)
      return <PaperSheet color="error" header='Symbol URLs not found!' message={this.state.errorMessage} />;

    const styles = theme => ({
      button: {
        margin: theme.spacing.unit,
      },
      leftIcon: {
        marginRight: theme.spacing.unit,
      },
      rightIcon: {
        marginLeft: theme.spacing.unit,
      },
      iconSmall: {
        fontSize: 20,
      },
      container: {
        display: 'flex',
        flexWrap: 'wrap',
      },
      textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 200,
      },
    });
    var quotesAll = _.map(this.state.resultsList, (resultValue) => {
      return <ListItem
        key={resultValue.Id}
      >
        <ListItemText primary={resultValue.name}

          secondary={
            <React.Fragment>

              <ExpansionPanel>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                  <div >
                    <Typography className={styles.heading}>{''}</Typography>
                  </div>
                  <div className={styles.column}>
                    <Typography className={styles.secondaryHeading}>{'Expand To View Details...'}</Typography>
                  </div>
                </ExpansionPanelSummary>
                <div >
                  <ExpansionPanelDetails>
                    {(resultValue.id === this.state.editId && this.state.editMode === true) ?
                      (
                        <TextField
                          id={'PERIOD_M15' + resultValue.id}
                          label="PERIOD_M15 Entry Length Limit"
                          style={{ margin: 8 }}
                          className={styles.textField}
                          defaultValue={this.state.PERIOD_M15}
                          onChange={this.handleTextFieldChange('PERIOD_M15')}
                          margin="normal"
                          variant="outlined"
                        />
                      ) :

                      <Typography>
                        PERIOD_M15 Entry Length Limit: {'' + ((resultValue.entriesTimeframeMaxLimit === isNullOrUndefined) ? ''
                          : '' + ((resultValue || {}).entriesTimeframeMaxLimit || {}).perioD_M15)
                        }
                      </Typography>

                    }
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelDetails>
                    {(resultValue.id === this.state.editId && this.state.editMode === true) ?
                      (
                        <TextField
                          id={'PERIOD_M30' + resultValue.id}
                          label="PERIOD_M30 Entry Length Limit"
                          style={{ margin: 8 }}
                          className={styles.textField}
                          defaultValue={this.state.PERIOD_M30}
                          onChange={this.handleTextFieldChange('PERIOD_M30')}
                          margin="normal"
                          variant="outlined"
                        />
                      ) :
                      <Typography>PERIOD_M30 Entry Length Limit: {'' + ((resultValue.entriesTimeframeMaxLimit === isNullOrUndefined) ? '' :
                        ((resultValue || {}).entriesTimeframeMaxLimit || {}).perioD_M30)
                      }</Typography>
                    }
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelDetails>
                    {(resultValue.id === this.state.editId && this.state.editMode === true) ?
                      (
                        <TextField
                          id={'PERIOD_H1' + resultValue.id}
                          label="PERIOD_H1 Entry Length Limit"
                          style={{ margin: 8 }}
                          className={styles.textField}
                          defaultValue={this.state.PERIOD_H1}
                          onChange={this.handleTextFieldChange('PERIOD_H1')}
                          margin="normal"
                          variant="outlined"
                        />
                      ) :
                      <Typography>PERIOD_H1 Entry Length Limit: {'' + ((resultValue.entriesTimeframeMaxLimit === isNullOrUndefined) ? '' :
                        ((resultValue || {}).entriesTimeframeMaxLimit || {}).perioD_H1)
                      }</Typography>
                    }
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelDetails>
                    {(resultValue.id === this.state.editId && this.state.editMode === true) ?
                      (
                        <TextField
                          id={'PERIOD_H4' + resultValue.id}
                          label="PERIOD_H4 Entry Length Limit"
                          style={{ margin: 8 }}
                          className={styles.textField}
                          defaultValue={this.state.PERIOD_H4}
                          onChange={this.handleTextFieldChange('PERIOD_H4')}
                          margin="normal"
                          variant="outlined"
                        />
                      ) :
                      <Typography>PERIOD_H4 Entry Length Limit: {'' + ((resultValue.entriesTimeframeMaxLimit === isNullOrUndefined) ? '' :
                        ((resultValue || {}).entriesTimeframeMaxLimit || {}).perioD_H4)
                      }</Typography>
                    }
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelDetails>
                    {(resultValue.id === this.state.editId && this.state.editMode === true) ?
                      (
                        <TextField
                          id={'PERIOD_D1' + resultValue.id}
                          label="PERIOD_D1 Entry Length Limit"
                          style={{ margin: 8 }}
                          className={styles.textField}
                          defaultValue={this.state.PERIOD_D1}
                          onChange={this.handleTextFieldChange('PERIOD_D1')}
                          margin="normal"
                          variant="outlined"
                        />
                      ) :
                      <Typography>PERIOD_D1 Entry Length Limit: {'' + ((resultValue.entriesTimeframeMaxLimit === isNullOrUndefined) ? '' :
                        ((resultValue || {}).entriesTimeframeMaxLimit || {}).perioD_D1)
                      }</Typography>
                    }
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelDetails>
                    {(resultValue.id === this.state.editId && this.state.editMode === true) ?
                      (
                        <TextField
                          id={'PERIOD_W1' + resultValue.id}
                          label="PERIOD_W1 Entry Length Limit"
                          style={{ margin: 8 }}
                          className={styles.textField}
                          defaultValue={this.state.PERIOD_W1}
                          onChange={this.handleTextFieldChange('PERIOD_W1')}
                          margin="normal"
                          variant="outlined"
                        />
                      ) :
                      <Typography>PERIOD_W1 Entry Length Limit: {'' + ((resultValue.entriesTimeframeMaxLimit === isNullOrUndefined) ? '' :
                        ((resultValue || {}).entriesTimeframeMaxLimit || {}).perioD_W1)
                      }</Typography>
                    }
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelDetails>
                    LotSize: {resultValue.lotSize}
                  </ExpansionPanelDetails>
                  <ExpansionPanelDetails>
                    DateAdded: {resultValue.dateAdded}
                  </ExpansionPanelDetails>
                  <ExpansionPanelDetails>
                    LastModifiedDate: {resultValue.lastModifiedDate}
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelActions>
                    {(resultValue.id === this.state.editId && this.state.editMode === true) ?
                      (
                        <Button onClick={() => this.updateSymbol(resultValue.id)} variant="outlined" color="primary" >
                          <Save className={classNames(styles.leftIcon, styles.iconSmall)} />
                          Update
                    </Button>
                      ) :
                      <Button onClick={() => this.editSymbol(resultValue.id)} variant="outlined" color="primary">
                        Edit
                     </Button>
                    }
                      <Button variant="contained" color="secondary" className={styles.button} onClick={() => this.removeSymbol(resultValue.id)}>
                       Delete
                       <Delete className={styles.rightIcon} />
                      </Button>
                  </ExpansionPanelActions>

                </div>
              </ExpansionPanel>
            </React.Fragment>

          }


        />

      </ListItem>;
    });


    return (
      <div>
        <BlockUi tag="div" blocking={this.state.blocking}>

        <form>
            <TextField
              id={'symbol_name'}
              label="Symbol Name"
              style={{ margin: 8 }}
              className={styles.textField}
              defaultValue={this.state.name}
              onChange={this.handleTextFieldChange('name')}
              margin="normal"
              variant="outlined"
            />

            <Button style={{ margin: 8 }} variant="contained" size="small" className={styles.button} onClick={this.postSymbol}>
              <Save className={classNames(styles.leftIcon, styles.iconSmall)} />
              Add/Save
            </Button>

          </form>

          <List subheader={<ListSubheader component="div">{'Symbols (' + this.state.totalPairs + ')'}</ListSubheader>}>
            {quotesAll}
          </List>
        </BlockUi>
      </div>
    );
  }

  postSymbol() {
    var url = BACKEND_BASE_URL + 'Patterns/Symbols';
    var body = {
      isDeleted: false,
      name: this.state.name,
    };
    this.toggleBlocking();
    fetch(url, {
      method: 'POST',
      body: JSON.stringify(body),
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
      .then(response => response.json())
      .then(data => {
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  removeSymbol(id) {
    var url = BACKEND_BASE_URL + 'Patterns/Symbols/' + id;
    this.toggleBlocking();
    fetch(url, {
      method: 'DELETE',
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
      .then(response => response.json())
      .then(data => {
        this.toggleBlocking();
        this.forceUpdate();
      }).catch((error) => {
        this.setState({
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  editSymbol(id) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        this.setState({
          PERIOD_W1: (!newArray[i].entriesTimeframeMaxLimit) ? '0' : newArray[i].entriesTimeframeMaxLimit.perioD_W1,
          PERIOD_D1: (!newArray[i].entriesTimeframeMaxLimit) ? '0' : newArray[i].entriesTimeframeMaxLimit.perioD_D1,
          PERIOD_H4: (!newArray[i].entriesTimeframeMaxLimit) ? '0' : newArray[i].entriesTimeframeMaxLimit.perioD_H4,
          PERIOD_H1: (!newArray[i].entriesTimeframeMaxLimit) ? '0' : newArray[i].entriesTimeframeMaxLimit.perioD_H1,
          PERIOD_M30: (!newArray[i].entriesTimeframeMaxLimit) ? '0' : newArray[i].entriesTimeframeMaxLimit.perioD_M30,
          PERIOD_M15: (!newArray[i].entriesTimeframeMaxLimit) ? '0' : newArray[i].entriesTimeframeMaxLimit.perioD_M15,
          editId: id,
          editMode: true
        });
        break; //Stop this loop, we found it!
      }
    }


  }

  updateSymbol(id) {
    var newArray = this.state.resultsList.slice();
    for (var i in newArray) {
      if (newArray[i].id === id) {
        var entriesTimeframeMaxLimit = {
          'PERIOD_W1': this.state.PERIOD_W1,
          'perioD_D1': this.state.PERIOD_D1,
          'perioD_H4': this.state.PERIOD_H4,
          'perioD_H1': this.state.PERIOD_H1,
          'perioD_M30': this.state.PERIOD_M30,
          'perioD_M15': this.state.PERIOD_M15
        };
        newArray[i].entriesTimeframeMaxLimit = entriesTimeframeMaxLimit;
        newArray[i].entriesTimeframeMaxLimitJson = JSON.stringify(entriesTimeframeMaxLimit);

        var url = BACKEND_BASE_URL + 'Patterns/Symbols';
        this.toggleBlocking();
        fetch(url, {
          method: 'PUT',
          body: JSON.stringify(newArray[i]),
          headers: new Headers({
            'Content-Type': 'application/json'
          })
        })
          .then(response => response.json())
          .then(data => {
            this.setState({ editMode: false });
            this.toggleBlocking();
          }).catch((error) => {
            this.setState({
              errorMessage: error.toString()
            });
            this.toggleBlocking();
          });
        this.setState({ resultsList: newArray });

        break; //Stop this loop, we found it!
      }
    }
  }

  getSymbols() {
    var url = BACKEND_BASE_URL + 'Patterns/Symbols';

    this.setState({
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      hasPendingHttp: false
    });
    this.toggleBlocking();
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.setState({
          resultsList: data,
          totalPairs: data.length,
          isFetching: false,
          hasHttpError: false
        });
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          isFetching: false,
          hasHttpError: true,
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  componentDidMount() {
    this.getSymbols();
  }
}