import React from 'react';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';
import Divider from '@material-ui/core/Divider';
import Typography from '@material-ui/core/Typography';

class Qoute extends React.Component {

    constructor(props) {
        super(props);
        this.getRandomQuote = this.getRandomQuote.bind(this);
        this.state = {
            quote: '',
            isFetching: true,
            hasHttpError: false,
            errorMessage: '',
        };
    }

    render() {

        if (this.state.isFetching) return <Loading />;
        if (this.state.hasHttpError)
            return <PaperSheet color="error" header='Quote not found!' message={this.state.errorMessage} />;
        return (
            <div style={{ whiteSpace: 'normal',backgroundColor: '#263238', color: 'white' }} >
                <Divider />
                <Typography variant="h6" color="inherit">
                   "{this.state.quote.description}" - {this.state.quote.fullname}
                </Typography>
                <Divider />
            </div>
        );
    }

    getRandomQuote() {
        var url = BACKEND_BASE_URL + 'assistance/Quotes/random';

        this.setState({
            isFetching: true,
            hasHttpError: false,
            errorMessage: '',
            quote: ''
        });
        fetch(url)
            .then(response => response.json())
            .then(data => {
                this.setState({
                    quote: data,
                    isFetching: false,
                    hasHttpError: false
                });
            }).catch((error) => {
                this.setState({
                    isFetching: false,
                    hasHttpError: true,
                    errorMessage: error.toString()
                });

            });
    }

    componentDidMount() {
        this.getRandomQuote();
    }
}

export default Qoute;