import React from 'react';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';
import _ from 'lodash';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ListItemText from '@material-ui/core/ListItemText';
import BlockUi from 'react-block-ui';
import 'react-block-ui/style.css';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import ExpansionPanelActions from '@material-ui/core/ExpansionPanelActions';

export default class Schedules extends React.Component {

  constructor(props) {
    super(props);
    this.getQuotes = this.getQuotes.bind(this);
    this.toggleBlocking = this.toggleBlocking.bind(this);
    this.state = {
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      hasPendingHttp: false,
      fullname: '',
      description: '',
      blocking: false,
    };
  }

  toggleBlocking() {
    this.setState({ blocking: !this.state.blocking });
  }

  handleTextFieldChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  render() {
    if (this.state.isFetching) return <Loading />;
    if (this.state.hasHttpError)
      return <PaperSheet color="error" header='Schedule URLs not found!' message={this.state.errorMessage} />;

    const styles = theme => ({
      button: {
        margin: theme.spacing.unit,
      },
      leftIcon: {
        marginRight: theme.spacing.unit,
      },
      rightIcon: {
        marginLeft: theme.spacing.unit,
      },
      iconSmall: {
        fontSize: 20,
      },
      container: {
        display: 'flex',
        flexWrap: 'wrap',
      },
      textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 200,
      },
    });
    var quotesAll = _.map(this.state.resultsList, (resultValue) => {
      return <ListItem
        key={resultValue.Id}
      >
        <ListItemText primary={resultValue.Url}

          secondary={
            <React.Fragment>

              <ExpansionPanel>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                  <div >
                    <Typography className={styles.heading}>{''}</Typography>
                  </div>
                  <div className={styles.column}>
                    <Typography className={styles.secondaryHeading}>{'Cron: (' + resultValue.Cron + '); CronPeriod: (' + resultValue.CronPeriod + ')____Expand To View Details...'}</Typography>
                  </div>
                </ExpansionPanelSummary>
                <div>
                  <ExpansionPanelDetails>
                    IsDisabled: {'' + resultValue.IsDisabled}
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelDetails>
                    Tag: {resultValue.Tag}
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelDetails>
                    Cron Period: {resultValue.CronPeriod}
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelDetails>
                    DateAdded: {resultValue.DateAdded}
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelActions>
                    <Button onClick={() => this.runSchedule(resultValue.Url)} variant="outlined" color="primary">
                      Schedule Now
                    </Button>
                  </ExpansionPanelActions>

                </div>

              </ExpansionPanel>
            </React.Fragment>

          }


        />

      </ListItem>;
    });


    return (
      <div>
        <BlockUi tag="div" blocking={this.state.blocking}>
          <List subheader={<ListSubheader component="div">{'URLs that are Scheduled (' + this.state.totalPairs + ')'}</ListSubheader>}>
            {quotesAll}
          </List>
        </BlockUi>
      </div>
    );
  }
  runSchedule(url) {
    url = url.replace("localhost", "sitholewb.co.za");
    this.toggleBlocking();
    fetch(url)
      .then(data => {
        this.toggleBlocking();
      }).catch((error) => {
        this.toggleBlocking();
      });
  }

  getQuotes() {
    var url = BACKEND_BASE_URL + 'schedule/ScheduleURLs';

    this.setState({
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      hasPendingHttp: false
    });
    this.toggleBlocking();
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.setState({
          resultsList: data,
          totalPairs: data.length,
          isFetching: false,
          hasHttpError: false
        });
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          isFetching: false,
          hasHttpError: true,
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  componentDidMount() {
    this.getQuotes();
  }
}