import React,  {Fragment} from 'react';
import Engulfing from './Engulfing';

const styles = {
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
  },
  gridList: {
    display: 'flex',
    flexWrap: 'nowrap',
    overflowX: 'auto',
  },
  titleStyle: {
    color: 'rgb(0, 188, 212)',
  },
};

export default class EngulfingsPage extends React.Component {

    constructor(props) {
      super(props);
      this.state = {
        numberOfInnerEngulfings: '1',
      };
    }
    state = {
      selectedTab: 'a',
    }
  
    handleTabClick = (event, value) => {
      this.setState({selectedTab: value});
    }

  render() {
    return (
    <Fragment>
      <div style={styles.root} >
          <Engulfing timeframe="PERIOD_MN1" nonfresh="false"/> 
          <Engulfing timeframe="PERIOD_W1" nonfresh="false"/>
          <Engulfing timeframe="PERIOD_D1" nonfresh="false"/>  
          <Engulfing timeframe="PERIOD_H4" nonfresh="false"/>  
          <Engulfing timeframe="PERIOD_H1" nonfresh="false"/>  
    </div>
    </Fragment>
    );
  }
}