import React from 'react';
import Audit from './Audit';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';

function TabContainer(props) {
  return (
    <Typography component="div" style={{ padding: 8 * 3 }}>
      {props.children}
    </Typography>
  );
}

export default class AuditsDashboard extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      numberOfInnerEngulfings: '1',
      value: 0
    };
  }

  handleChangeNumberOfInnerEngulfings = (event) => {
    this.setState({
      numberOfInnerEngulfings: event.target.value,
    });
  };
  state = {
    value: 0,
  };

  handleChange = (event, value) => {
    this.setState({ value });
  };
  render() {
    const { value } = this.state;
    return (
      <div>
        <Tabs value={value} onChange={this.handleChange} scrollable scrollButtons="off">
          <Tab label="Trader API Audits" />
          <Tab label="Assistance API Audits" />
          <Tab label="Trades API Audits" />
        </Tabs>
        {value === 0 && <TabContainer> <Audit apiName="Trader" /></TabContainer>}
        {value === 1 && <TabContainer> <Audit apiName="Assistance" /></TabContainer>}
        {value === 2 && <TabContainer> <Audit apiName="Trades" /></TabContainer>}
      </div>
    );
  }
}