import React from 'react';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ListItemText from '@material-ui/core/ListItemText';
import _ from 'lodash';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';


const styles = theme => ({
  button: {
    margin: theme.spacing.unit,
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit,
    width: 200,
  },
});

export default class ErrorLog extends React.Component {

  constructor(props) {
    super(props);
    this.getAllResultValues = this.getAllResultValues.bind(this);
    this.state = {
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      errorsCount: 5
    };
  }

  handleChangeIndex = (event) => {
    this.setState({
      errorsCount: event.target.value,
    });
  };

  render() {
    if (this.state.isFetching) return <Loading />;
    if (this.state.hasHttpError)
      return <PaperSheet color="error" header={this.props.apiName} message={this.state.errorMessage} />;

    var allResultValues = _.map(this.state.resultsList, (resultValue) => {
      return <ListItem key={resultValue.id}>
        <ListItemText primary={resultValue.message}

          secondary={
            <React.Fragment>

              <ExpansionPanel>
                <ExpansionPanelSummary expandIcon={<ExpandMoreIcon />}>
                  <div >
                    <Typography className={styles.heading}>{''}</Typography>
                  </div>
                  <div className={styles.column}>
                    <Typography className={styles.secondaryHeading}>{'Date: (' + resultValue.dateAdded + ');_____Expand To View Exception Details...'}</Typography>
                  </div>
                </ExpansionPanelSummary>
                <div>
                  <ExpansionPanelDetails>
                    {resultValue.request}
                  </ExpansionPanelDetails>
                  <Divider />
                  <ExpansionPanelDetails>
                    {resultValue.exception}

                  </ExpansionPanelDetails>
                </div>

              </ExpansionPanel>
            </React.Fragment>

          }
        />
        <Divider />
      </ListItem>;
    });
    return (
      <List subheader={<ListSubheader component="div">{'Found (' + this.state.totalPairs + ')'}</ListSubheader>}>
        <div>
          <TextField
            label="Last N"
            id="handleChangeIndex-none"
            onChange={this.handleChangeIndex}
            value={this.state.errorsCount}
            className={styles.textField}
          />

          <Button variant="contained" size="small" className={styles.button} onClick={this.getAllResultValues}>Refresh</Button>
        </div>
        {allResultValues}
      </List>
    );
  }

  getAllResultValues() {
    var url = BACKEND_BASE_URL + this.props.apiName + '/errorlogs/count/' + this.state.errorsCount;

    this.setState({
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      resultsList: []
    });
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.setState({
          resultsList: data,
          totalPairs: data.length,
          isFetching: false,
          hasHttpError: false
        });
      }).catch((error) => {
        this.setState({
          isFetching: false,
          hasHttpError: true,
          errorMessage: error.toString()
        });

      });
  }
  componentDidMount() {
    this.getAllResultValues();
  }
}