import React from 'react';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';
import _ from 'lodash';
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import Button from '@material-ui/core/Button';
import Save from '@material-ui/icons/Save';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Delete from '@material-ui/icons/Delete';
import classNames from 'classnames';
import BlockUi from 'react-block-ui';
import 'react-block-ui/style.css';

export default class Qoutes extends React.Component {

  constructor(props) {
    super(props);
    this.getQuotes = this.getQuotes.bind(this);
    this.postQuote = this.postQuote.bind(this);
    this.toggleBlocking = this.toggleBlocking.bind(this);
    this.state = {
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      hasPendingHttp: false,
      fullname: '',
      description: '',
      blocking: false,
    };
  }

  toggleBlocking() {
    this.setState({ blocking: !this.state.blocking });
  }

  handleTextFieldChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  render() {
    if (this.state.isFetching) return <Loading />;
    if (this.state.hasHttpError)
      return <PaperSheet color="error" header='Quote not found!' message={this.state.errorMessage} />;

    const styles = theme => ({
      button: {
        margin: theme.spacing.unit,
      },
      leftIcon: {
        marginRight: theme.spacing.unit,
      },
      rightIcon: {
        marginLeft: theme.spacing.unit,
      },
      iconSmall: {
        fontSize: 20,
      },
      container: {
        display: 'flex',
        flexWrap: 'wrap',
      },
      textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 200,
      },
    });
    var quotesAll = _.map(this.state.resultsList, (quote) => {
      return <ListItem
        key={quote.id}
      >
        <ListItemText primary={quote.description} secondary={quote.fullname} />

        <ListItemSecondaryAction>

          <Button variant="contained" color="secondary" className={styles.button} onClick={() => this.removeQuote(quote.id)}>
            Delete
            <Delete className={styles.rightIcon} />
          </Button>
        </ListItemSecondaryAction>

      </ListItem>;
    });


    return (
      <div>
        <BlockUi tag="div" blocking={this.state.blocking}>
          <form>
            <TextField
              id={'fullname_' + this.state.id}
              label="Fullname"
              style={{ margin: 8 }}
              className={styles.textField}
              defaultValue={this.state.fullname}
              onChange={this.handleTextFieldChange('fullname')}
              margin="normal"
              variant="outlined"
            />
            <TextField
              id={'description_' + this.state.id}
              label="Description"
              style={{ margin: 8 }}
              placeholder="Placeholder"
              onChange={this.handleTextFieldChange('description')}
              defaultValue={this.state.description}
              fullWidth
              margin="normal"
              variant="outlined"
            />

            <Button style={{ margin: 8 }} variant="contained" size="small" className={styles.button} onClick={this.postQuote}>
              <Save className={classNames(styles.leftIcon, styles.iconSmall)} />
              Add/Save
            </Button>

          </form>


          <List subheader={<ListSubheader component="div">{'Quotes (' + this.state.totalPairs + ')'}</ListSubheader>}>
            {quotesAll}
          </List>
        </BlockUi>
      </div>
    );
  }

  postQuote() {
    var url = BACKEND_BASE_URL + 'assistance/Quotes';
    var body = {
      description: this.state.description,
      fullname: this.state.fullname,
    };
    this.toggleBlocking();
    fetch(url, {
      method: 'POST',
      body: JSON.stringify(body),
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
      .then(response => response.json())
      .then(data => {
        this.getQuotes();
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  getQuotes() {
    var url = BACKEND_BASE_URL + 'assistance/Quotes';

    this.setState({
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      hasPendingHttp: false
    });
    this.toggleBlocking();
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.setState({
          resultsList: data,
          totalPairs: data.length,
          isFetching: false,
          hasHttpError: false
        });
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          isFetching: false,
          hasHttpError: true,
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  removeQuote(id) {
    var url = BACKEND_BASE_URL + 'assistance/Quotes/' + id;
    this.toggleBlocking();
    fetch(url, {
      method: 'DELETE',
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
      .then(response => response.json())
      .then(data => {
        this.getQuotes();
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  componentDidMount() {
    this.getQuotes();
  }
}