import React from 'react';
import { BACKEND_BASE_URL } from '../shared/Urls.json';
import PaperSheet from './PaperSheet';
import Loading from 'react-loading-animation';
import _ from 'lodash';
import TextField from '@material-ui/core/TextField';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import Button from '@material-ui/core/Button';
import Save from '@material-ui/icons/Save';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Delete from '@material-ui/icons/Delete';
import classNames from 'classnames';
import BlockUi from 'react-block-ui';
import 'react-block-ui/style.css';

export default class SuspendedTrades extends React.Component {

  constructor(props) {
    super(props);
    this.getSuspendedTrades = this.getSuspendedTrades.bind(this);
    this.postSuspendedTrade = this.postSuspendedTrade.bind(this);
    this.toggleBlocking = this.toggleBlocking.bind(this);
    this.suspendAllTrades = this.suspendAllTrades.bind(this);
    this.state = {
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      hasPendingHttp: false,
      symbol: '',
      timeframe: '',
      comment:'',
      blocking: false,
    };
  }

  toggleBlocking() {
    this.setState({ blocking: !this.state.blocking });
  }

  handleTextFieldChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  render() {
    if (this.state.isFetching) return <Loading />;
    if (this.state.hasHttpError)
      return <PaperSheet color="error" header='SuspendedTrades not found!' message={this.state.errorMessage} />;

    const styles = theme => ({
      button: {
        margin: theme.spacing.unit,
      },
      leftIcon: {
        marginRight: theme.spacing.unit,
      },
      rightIcon: {
        marginLeft: theme.spacing.unit,
      },
      iconSmall: {
        fontSize: 20,
      },
      container: {
        display: 'flex',
        flexWrap: 'wrap',
      },
      textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 200,
      },
    });
    var suspendedTradesAll = _.map(this.state.resultsList, (suspendedTrade) => {
      return <ListItem
        key={suspendedTrade.id}
      >
        <ListItemText primary={suspendedTrade.timeframe + ' <=> ' + suspendedTrade.symbol } 
        secondary={'Comment: '+ suspendedTrade.comment} />

        <ListItemSecondaryAction>

          <Button variant="contained" color="secondary" className={styles.button} onClick={() => this.removeSuspendedTrade(suspendedTrade.id)}>
            Delete
            <Delete className={styles.rightIcon} />
          </Button>
        </ListItemSecondaryAction>

      </ListItem>;
    });


    return (
      <div>
        <BlockUi tag="div" blocking={this.state.blocking}>
          <form>
            <TextField
              id={'symbol_' + this.state.id}
              label="Symbol"
              style={{ margin: 8 }}
              className={styles.textField}
              defaultValue={this.state.symbol}
              onChange={this.handleTextFieldChange('symbol')}
              margin="normal"
              variant="outlined"
            />
            <TextField
              id={'timeframe_' + this.state.id}
              label="Timeframe"
              style={{ margin: 8 }}
              placeholder="Placeholder"
              onChange={this.handleTextFieldChange('timeframe')}
              defaultValue={this.state.timeframe}
              margin="normal"
              variant="outlined"
            />
            <TextField
              id={'comment_' + this.state.id}
              label="Comment"
              style={{ margin: 8 }}
              placeholder="Placeholder"
              onChange={this.handleTextFieldChange('comment')}
              defaultValue={this.state.comment}
              fullWidth
              margin="normal"
              variant="outlined"
            />

            <Button style={{ margin: 8 }} variant="contained" size="small" className={styles.button} onClick={this.postSuspendedTrade}>
              <Save className={classNames(styles.leftIcon, styles.iconSmall)} />
              Add/Save
            </Button>

          </form>
          <Button size="small" className={styles.button} onClick={this.suspendAllTrades}>Suspend Robot From Taking Trades</Button>

          <List subheader={<ListSubheader component="div">{'SuspendedTrades (' + this.state.totalPairs + ')'}</ListSubheader>}>
            {suspendedTradesAll}
          </List>
        </BlockUi>
      </div>
    );
  }

  postSuspendedTrade() {
    var url = BACKEND_BASE_URL + 'trader/SuspendedTrades';
    var body = {
      timeframe: this.state.timeframe,
      symbol: this.state.symbol,
      comment: this.state.comment
    };
    this.toggleBlocking();
    fetch(url, {
      method: 'POST',
      body: JSON.stringify(body),
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
      .then(response => response.json())
      .then(data => {
        this.toggleBlocking();
        this.forceUpdate();
      }).catch((error) => {
        this.setState({
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  getSuspendedTrades() {
    var url = BACKEND_BASE_URL + 'trader/SuspendedTrades';

    this.setState({
      resultsList: [],
      totalPairs: 0,
      isFetching: true,
      hasHttpError: false,
      errorMessage: '',
      hasPendingHttp: false
    });
    this.toggleBlocking();
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.setState({
          resultsList: data,
          totalPairs: data.length,
          isFetching: false,
          hasHttpError: false
        });
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          isFetching: false,
          hasHttpError: true,
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  removeSuspendedTrade(id) {
    var url = BACKEND_BASE_URL + 'trader/SuspendedTrades/' + id;
    this.toggleBlocking();
    fetch(url, {
      method: 'DELETE',
      headers: new Headers({
        'Content-Type': 'application/json'
      })
    })
      .then(response => response.json())
      .then(data => {
        this.toggleBlocking();
        this.forceUpdate();
      }).catch((error) => {
        this.setState({
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  suspendAllTrades() {
    var url = BACKEND_BASE_URL + 'trader/SuspendedTrades/suspendAll';
    this.toggleBlocking();
    fetch(url)
      .then(response => response.json())
      .then(data => {
        this.toggleBlocking();
      }).catch((error) => {
        this.setState({
          errorMessage: error.toString()
        });
        this.toggleBlocking();
      });
  }

  componentDidMount() {
    this.getSuspendedTrades();
  }
}